:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --lightBlack: hsla(0, 0%, 0%, 0.137);
  --emphasis:#ffa575;
  --base-font: #fff;
  --background-color: #497281;
}

body {
  background-color: var(--background-color);
  color: var(--base-font);
}

.about {
  color: var(--emphasis);
}

svg#info-icon {
  fill: var(--emphasis);
}

a, a:hover, a:visited {
  text-decoration: none;
  color: var(--emphasis);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0rem;
  padding: 4rem;
}
@media (max-width: 71.8125em) {
  body {
    margin: 0rem;
    padding: 1rem;
  }
}

:root {
  --font-body: Arial, sans-serif;
}

body {
  font-family: "Space Mono", monospace;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 6rem;
  font-weight: 900;
  padding: 0;
  margin: 0;
}

h2 {
  font-size: 1.5rem;
  font-weight: 500;
  padding: 0;
  margin: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  cursor: pointer;
}

svg.title {
  font: bold 50px Montserrat Alternates, Arial;
  height: 120px;
}

text {
  fill: none;
  stroke: white;
  stroke-width: 0.5px;
  stroke-linejoin: round;
  stroke-dasharray: 4 1;
}

.container {
  width: 100%;
  padding: 1rem;
}

.item {
  position: relative;
  width: 20rem;
  margin: 1rem;
}
@media (max-width: 71.8125em) {
  .item {
    max-width: 15rem;
    margin: 0.5rem;
  }
}

img {
  max-width: 100%;
  cursor: pointer;
}

.header {
  padding-top: 1rem;
  padding-bottom: 1.5rem;
  margin: 0 auto;
  width: 100%;
  flex-wrap: wrap;
}
.header .title {
  width: 80%;
}
@media (max-width: 39.9375em) {
  .header .title {
    width: 100%;
  }
}
.header .description {
  margin-top: 1.5rem;
}
.header .about {
  display: fixed;
  width: 20%;
  text-align: right;
  cursor: pointer;
}
@media (max-width: 39.9375em) {
  .header .about {
    margin-top: 1.5rem;
    width: 100%;
  }
}

.flex {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.flex-row {
  flex-direction: row;
}

.flex-col {
  flex-direction: column;
}

.flex-space-around {
  justify-content: space-around;
}

.flex-space-between {
  justify-content: space-evenly;
}

.flex-start {
  justify-content: flex-start;
}

.modal {
  position: fixed;
  top: 0%;
  right: 0%;
  height: 100vh;
  width: 100vw;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: -1;
}
.modal.visible {
  opacity: 1;
  z-index: 1;
}
.modal .wrapper {
  max-width: 80%;
}
.modal .wrapper::after {
  content: "";
  position: absolute;
  right: 7%;
  top: 7%;
  width: 2rem;
  height: 0.2rem;
  background-color: var(--base-font);
  transform-origin: 50% 50%;
  transform: rotate(45deg);
  border-radius: 20%;
}
.modal .wrapper::before {
  content: "";
  position: absolute;
  right: 7%;
  top: 7%;
  width: 2rem;
  height: 0.2rem;
  background-color: var(--base-font);
  transform-origin: 50% 50%;
  transform: rotate(-45deg);
  border-radius: 20%;
}
.modal .img-selected {
  position: relative;
  width: 100%;
  max-height: 80vh;
}
.modal .img-description {
  position: absolute;
  width: 50%;
  text-align: left;
}
.modal .about-description {
  position: absolute;
  width: 50%;
  left: 10%;
  text-align: left;
}

.moving-diag {
  animation-duration: 20s;
  animation-name: move-diag;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

.moving-horiz-diag {
  animation-duration: 25s;
  animation-name: move-horiz-diag;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

.moving-horiz-diag2 {
  animation-duration: 30s;
  animation-name: move-horiz-diag2;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

.rotating {
  animation-duration: 20s;
  transform-origin: 50% 50%;
  animation-name: rotate;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

.rotating-2 {
  animation-duration: 30s;
  transform-origin: 50% 50%;
  animation-name: rotate-2;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

.moving-y {
  animation-duration: 25s;
  transform-origin: 50% 50%;
  animation-name: move-y;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes move-diag {
  0% {
    transform: translate(-30px, -30px);
  }
  50% {
    transform: translate(30px, 30px);
  }
  100% {
    transform: translate(-30px, -30px);
  }
}
@keyframes move-y {
  0% {
    transform: translate(0px, 60px);
  }
  50% {
    transform: translate(0px, -80px);
  }
  100% {
    transform: translate(0px, 60px);
  }
}
@keyframes move-horiz-diag {
  0% {
    transform: translate(50px, -50px);
  }
  30% {
    transform: translate(10px, 50px);
  }
  60% {
    transform: translate(-50px, -50px);
  }
  100% {
    transform: translate(50px, -50px);
  }
}
@keyframes move-horiz-diag2 {
  0% {
    transform: translate(50px, -80px);
  }
  30% {
    transform: translate(10px, 50px);
  }
  60% {
    transform: translate(50px, -50px);
  }
  100% {
    transform: translate(50px, -80px);
  }
}
@keyframes rotate {
  0% {
    transform: rotate(-10deg);
  }
  30% {
    transform: rotate(10deg);
  }
  100% {
    transform: rotate(-10deg);
  }
}
@keyframes rotate-2 {
  0% {
    transform: rotate(-30deg);
  }
  30% {
    transform: rotate(40deg);
  }
  100% {
    transform: rotate(-30deg);
  }
}
@keyframes rotate-y {
  0% {
    transform: rotate(0, -20deg);
  }
  30% {
    transform: rotate(0, 20deg);
  }
  100% {
    transform: rotate(0, -20deg);
  }
}